.modal{display:flex;justify-content:center;align-items:center;position:fixed;z-index:1;width:100%;height:100%;overflow:auto;background-color:#000;background-color:rgba(0,0,0,0.4)}.modal-content{background-color:#fefefe;opacity:0.7;height:70%;width:70%;padding:20px;border-radius:25px;border:1px solid #888;-webkit-animation:createBox 1s;animation:createBox 1s}@-webkit-keyframes createBox{from{transform:scale(0)}to{transform:scale(1)}}@keyframes createBox{from{transform:scale(0)}to{transform:scale(1)}}.close{color:#aaaaaa;float:right;font-size:28px;font-weight:bold}.close:hover,.close:focus{color:#000;text-decoration:none;cursor:pointer}
