html,body{margin:0;padding:0}.canvas{position:absolute;background-image:url(images/bg.jpg);background-size:cover;background-repeat:no-repeat;background-position:center center;width:100%;height:100%;opacity:0.5;margin:0px auto;z-index:-9999;-webkit-animation:fadeInAnimation ease 2s;animation:fadeInAnimation ease 2s;-webkit-animation-iteration-count:1;animation-iteration-count:1;-webkit-animation-fill-mode:forwards;animation-fill-mode:forwards}@-webkit-keyframes fadeInAnimation{0%{opacity:0}100%{opacity:0.5}}@keyframes fadeInAnimation{0%{opacity:0}100%{opacity:0.5}}#user-output{height:100%;width:100%;display:flex;justify-content:center;align-items:center}#user-output>p{color:black;font-family:helvetica;font-weight:bold;font-size:50px}#timer{display:flex;justify-content:center;align-items:center;height:100%}
